// Stores the state of the application
const states = {};

// Maps the virtual DOM to the real DOM
const render = (vdom, container) => {
    const renderedElement = document.createElement(vdom.tag);

    if (vdom.props) {
        Object.keys(vdom.props)
            .filter(key => key !== 'children')
            .forEach(name => renderedElement[name] = vdom.props[name]);
    }

    if (vdom.props.children) {
        vdom.props.children.forEach(child => typeof child === 'object' ? render(child, renderedElement) : renderedElement.appendChild(document.createTextNode(child)));
    }

    container.appendChild(renderedElement);
}


const App = () =>  {
    const [state, setState] = React.useState(0, 'counter');

    return (
    <div className="foo">
        <h1>Hello World</h1>
        <p>This is my first virtual DOM</p>

        <p>State: {state}</p>
        <button onclick={(...all) => setState(state + 1)}>+</button>
        <button onclick={() => setState(state - 1)}>-</button>
    </div>
    );
}

window.onload = () => render(<App />, document.getElementById('app'));
